:root {
  --box-shadow: 0 0.1rem 1rem rgba(0, 0, 0, 0.1);
   --navbar-height: 80px;
}
/* Navbar */
.main-content {
  padding-top: 100px;
  padding-bottom: 20px; /* SAME AS NAVBAR HEIGHT */
}

.navbar {
  align-items: center;
  justify-content: center;
  display: flex;
  height: 100px;
  z-index: 999;
  top: 0;
  position: fixed; /* Changed from 'position: top' to 'position: fixed' */
  width: 100%; /* Added to make sure the navbar spans the full width of the page */
  margin: 0;
  padding: 0;
  box-shadow: var(--box-shadow);
  background-color: white; /* You may want to set a background color */
}

.navbar_container {
  align-items: center;
  justify-content: space-between;
  display: flex;
  height: 100px;
  z-index: 1;
  width: 80%;
  margin: 0 auto;
}

.logo_container {
  align-items: center;
  justify-content: center;
  display: flex;
}

.logo_link {
  text-decoration: none;
  align-items: center;
  justify-content: center;
  display: flex;
}

.logo {
  width: 100%;
  height: 100px;
  justify-content: center;
  align-items: center;
}

.logo_showcase {
  width: 80px;
  height: 80px;
}

.navbar_toggle {
  display: none;
  transition: all 0.3s ease-in-out;
}

.navbar_menu {
  list-style: none;
  align-items: center;
  display: flex;
  margin: 0;
  padding: 0;
}

.menu_items {
  height: 80px;
}

.navbar_links {
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: flex;
  width: 125px;
  height: 100%;
  text-decoration: none;
  color: #133296a6;
  font-size: 15px;
  transition: 0.3s all ease;
}


.navbar_links:hover {
  transition: 0.3s all ease;
  color: #e96b56;
}

.navbar_btn {
  padding: 0 1rem;
  align-items: center;
  justify-content: center;
  display: flex;
}

.navbar_links svg {
  font-size: 20px;
  margin-bottom: 4px;
}

/* Small hint text under icon */
.nav-label {
  font-size: 11px;
  line-height: 1;
  letter-spacing: 0.5px;
  color: #133296a6;
  transition: color 0.3s ease;
}
@media (max-width: 768px) {
  .nav-label {
    font-size: 10px;
  }
}

.button {
  align-items: center;
  justify-content: center;
  display: flex;
  text-decoration: none;
  border: 1px solid var(--bs-primary);
  border-radius: 4px;
  outline: none;
  transition: ease-out 0.3s;
  padding: 10px 20px;
  color: #666;
  position: relative;
  z-index: 1;
  font-size: 18px;
  flex-direction: column;
}

.button-text { 
  font-size: 14px; 
  color: #666; 
  margin-top: 5px; /* Adjust the spacing between the button and the text */ 
  text-align: center; /* Center the text under the button */ }

.button:hover {
  color: #fff;
  cursor: pointer;
}
.button:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: var(--bs-primary);
}
.button1 {
  align-items: center;
  justify-content: center;
  display: flex;
  text-decoration: none;
  border: 1px solid var(--bs-danger);
  border-radius: 4px;
  outline: none;
  transition: ease-out 0.3s;
  padding: 10px 20px;
  color: #666;
  position: relative;
  z-index: 1;
  font-size: 18px;
}
.button1:hover {
  color: #fff;
  cursor: pointer;
}
.button1:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: var(--bs-danger);
}
.leaflet-container {
  height: 400px;
}
.button1:hover:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
  z-index: -1;
}
.img1{
  background-size:contain;
