@media screen and (max-width: 960px) {
  .navbar_container {
    align-items: center;
    justify-content: space-between;
    display: flex;
    width: 100%;
    margin: 0;
    z-index: 1;
    height: 80px;
  }
  .navbar_menu {
    display: grid;
    grid-template-columns: auto;
    top: -1000px;
    position: absolute;
    margin: 0;
    width: 100%;
    opacity: 1;
    transition: all 0.3s ease;
    z-index: -1;
  }
  .navbar_menu.active {
    background-color: #fff;
    z-index: 99;
    top: 100%;
    opacity: 1;
    transition: all 0.5s ease;
    height: auto;
  }
  .logo_container {
    margin-left: 25px;
  }
  .navbar_toggle {
    align-items: center;
    justify-content: center;
    display: block;
    margin-right: 25px;
    transition: 0.3s all ease-in-out;
    font-size: 18px;
    color: #133296a6;
    cursor: pointer;
  }
  .menu_items {
    height: 100%;
  }
  .navbar_links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }
  .navbar_btn {
    margin-bottom: 2rem;
  }
  .button {
    align-items: center;
    justify-content: center;
    display: flex;
    width: 80%;
    height: 80px;
    margin: 0;
  }
}
/*Breadcrumbs*/
.breadcrumbs {
  padding: 0px 0 0px 0;
  background: #ffffff;
  border-bottom: 1px solid #ffffff;
}

.breadcrumbs h2 {
  
  font-weight: 700;
  color: #133296a6;
}

.breadcrumbs ol {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0 0 5px 0;
  margin: 0;
  color: #e96b56;
  font-size:22px;
}

.breadcrumbs ol li + li {
  padding-left: 10px;
}

.breadcrumbs ol li + li::before {
  display: inline-block;
  padding-right: 10px;
  color: #6e6e6e;
  content: "/";
}
.text {
  gap: 30px;
}

/* Hero section*/
#hero {
  width: 90%;
  height: 90vh;
  background-size: cover;
  position: relative;
  padding-top: 80px;
}
#hero .container {
  z-index: 2;
}

#hero h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  line-height: 56px;
  color: #fff;
}

#hero h1 span {
  border-bottom: 4px solid #3498db;
}

#hero h2 {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 30px;
  font-size: 24px;
}

#hero .btn-get-started {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 2px;
  display: inline-block;
  padding: 12px 28px;
  border-radius: 4px;
  transition: ease-in-out 0.3s;
  color: #fff;
  background: #3498db;
  text-transform: uppercase;
}

#hero .btn-get-started:hover {
  background: #4ea5e0;
}

@media (max-width: 992px) {
  #hero {
    height: calc(100vh - 70px);
  }
}

@media (max-width: 768px) {
  #hero h1 {
    font-size: 30px;
    line-height: 36px;
  }

  #hero h2 {
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 30px;
  }
}
